.ant-table-thead tr th{
    background: #BFE1E8 !important;
}
.components-table-demo-nested{
    max-width: 1300px;
    width: 100%;
    margin-top: 100px;
}
.components-table-demo-nested td {
    color: #003642;
    border-right: 1px solid #003642;
}
.section__all__product{
    width: 100%;
    box-sizing: border-box;
    padding: 30px;
    display: flex;
    justify-content: center;
}
.ant-table-row-expand-icon-cell {
    border: none !important;
}
.ant-table-cell {
    text-align: center;
    border: none;
}
.sub__table .ant-table-thead tr th {
    background: #BFE8E8 !important;
    text-align: center;
}
.sub__table  tr  {
    background: #D1D1D1 !important;
}
.sub__table .ant-table-cell {
    padding: 6px !important;
}
.sub__table  tr:hover {
    background: #BFE1E8 !important;
}
.sub__table  tr td {
    border: 1px solid #ffffff;
    color: #003642;
    height: 20px !important;
}
.child_title {
    padding: 0;
    margin: 0;
}
.ant-table-expanded-row-level-1{
    background: #777777 !important;
}
@media only screen and (max-width:1200px) {
    .ant-table-content {
        overflow-x: scroll;
    }
}
@media only screen and (max-width:425px) {
    .ant-table-thead > tr > th {
        font-size: 10px;
    }
}